import { Controller, Commands } from '@ali/4ever-cangjie';
import {
  clearDropHolder,
  updateDropHolder,
  IUpdateImageDropHolderPayload,
} from '../actions';
import dragOverInlineHandler from '../dragEventHandlers/dragOverInlineHandler';

export default function updateImageDropHolder(controller: Controller, payload: IUpdateImageDropHolderPayload) {
  const { dropHolder, dispatch, event } = payload;
  if (dropHolder) {
    controller.command(Commands.blur);
    dispatch(updateDropHolder(dropHolder));
  } else {
    controller.command(Commands.focus);
    dragOverInlineHandler(event, controller);
    dispatch(clearDropHolder());
  }
}
